import {DetailsWidget} from "../../challenge/widget/details";
import React, {useCallback, useContext, useEffect} from "react";
import styled from "styled-components";
import {ButtonWidget} from "../../../widget/pub/button_widget";
import {ModalContext} from "../../../provider/model_provider";
import {TisArbitrationModel} from "../widget/tis_arbitration_model";
import {useWait} from "../../../utils/wait_fn";
import {HttpServer} from "../../../net/http_server";
import { useParams } from "react-router-dom";
import {DetailsInterface} from "../../../interface/challenge/details";

export const ArbitrationPage: React.FC = () => {
    const {onPresent} = useContext(ModalContext);
    const {data,request} = useWait<DetailsInterface>(HttpServer.assessment)
    const  params = useParams<{id:string}>();
    const handleArbitration = useCallback((type:string) => {
        onPresent(<TisArbitrationModel updateFn={request}  claim_hash={data.cover_hash} claim_id={data.claim_id} type={type}/>)
    }, [data,request])


    useEffect(()=>{
        request(params.id);
    },[])



    return (
        <WapStyle>
            <div className="title">
                ASESSMENT
            </div>
            <DetailsWidget model={data}/>
            <div className="buttons">
                <ButtonWidget
                    onClick={()=>handleArbitration('Pass')}
                    bgColor={'#0064FD'}
                    disable={data.status != 'arbiter'}
                    text={'Claim Pass'}
                    size={{x: 10, y: 40}}
                />
                <ButtonWidget
                    onClick={()=>handleArbitration('Deny')}
                    bgColor={'#0064FD'}
                    text={'Claim Deny'}
                    disable={data.status != 'arbiter'}
                    size={{x: 10, y: 40}}
                />
            </div>


        </WapStyle>
    );
}

const WapStyle = styled.div`
  background-color: white;

  .buttons {
    display: flex;
    justify-content: space-around;
    padding: 50px 0;
    width: 60%;
    margin: 0 auto;
  }

  .title {
    height: 55px;
    background-color: #EAF2FF;
    line-height: 55px;
    padding-left: 20px;
  }
`
