import React, {useCallback, useContext, useEffect, useState} from "react";
import {HttpStatus, useWait} from "../utils/wait_fn";

import {ModalContext} from "../provider/model_provider";
import styled from "styled-components";
import ReactLoading from "react-loading";
import errorImg from "../assets/imgs/error_warning.png"
import sueccessImg from "../assets/imgs/http_suecess.png"
import feiImg from "../assets/imgs/fei.png"
import {UrlHaxFormat} from "../utils/url";
import {ButtonWidget} from "../widget/pub/pub";


export const LoadingModal: React.FC<{
    data: any,
    status: HttpStatus,
    onDismiss?: any,
    success?: React.FC<any>,
    transfer?: boolean
}> = ({
          data,
          children,
          onDismiss,
          status,
          success,
          transfer
      }) => {

    const [widget, setWidget] = useState<any>(children);

    useEffect(() => {


        if (status === HttpStatus.failure) {
            if (data.err.code && data.err.code === 4001) {
                onDismiss()
            }
            setWidget(<LoadingWapStyle><ErrWidget msg={data.err.msg}/></LoadingWapStyle>)
        }
        if (status === HttpStatus.wait) {
            setWidget(<LoadingWapStyle><WaitWidget/></LoadingWapStyle>)
        }
        if (status === HttpStatus.success) {

            if (success) {
                setWidget(React.createElement(success, {onDismiss: onDismiss}, null))
                return
            }
            if (transfer) {
                setWidget(<LoadingWapStyle><TransferWidget hash={data} onDismiss={onDismiss}/></LoadingWapStyle>)
                return;
            }
            setWidget(<LoadingWapStyle><SuccessWidget/></LoadingWapStyle>)
        }


    }, [status])
    return (
        <div>
            {widget}
        </div>
    );
}


export const LoadingWidget: React.FC<{
    confirmationFn: Function,
    onDismiss?: any,
    successWidget?: React.FC<any>
}> = ({
          confirmationFn,
          onDismiss,
          successWidget
      }) => {
    const {request, status, data} = useWait<any>(confirmationFn);
    useEffect(() => {
        request()
    }, [])
    return (
        <LoadingModal onDismiss={onDismiss} success={successWidget} data={data} status={status}/>
    );
}


/**
 * 使用 SendModal 带参数时使用
 * @param confirmationFn
 */
export const useSendModal = (confirmationFn: Function, node?: React.FC) => {
    const {onPresent} = useContext(ModalContext);
    const onWithdraw = useCallback((...res) => {
        onPresent(<LoadingWidget confirmationFn={() => confirmationFn(...res)} successWidget={node}/>);
    }, [])
    return onWithdraw;
}


