import React, {useCallback, useContext} from "react";
import styled from "styled-components";
import {ButtonWidget} from "../../../widget/pub/button_widget";
import {useModel} from "../../../hook/use_model";
import {StakeModel} from "../../../model/stake_model";
import {BackdropContext} from "../../../provider/backdrop_provider";
import {useParams} from "react-router-dom";
import {useChallenge, useChallengeP} from "../fn/challenge";
import {ArbitrationInterface, ArbitrationProvider} from "../arbitration";


export const AsessmentBottomWidget: React.FC = () => {
    const params = useParams<{ id: string }>();
    const arbitrationModel = useContext<ArbitrationInterface>(ArbitrationProvider);


    const handleChallenge = useChallenge(params.id,arbitrationModel?.claim?.cover_hash||'');

    return (<AsessmentStyle>
        <span>Claim {arbitrationModel?.claim?.status}</span>
        <ButtonWidget onClick={handleChallenge} bgColor={'#0064FD'} text={'Challenge'} size={{x:10,y:40}}/>
    </AsessmentStyle>);
}





const AsessmentStyle = styled.div`
  display: flex;
  width: 100%;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  margin: 60px 0 30px 0;
  span {
    font-size: 18px;
    color: #30B29A;
    display: block;
    margin-bottom: 10px;
   
  }
`
