import NetAxios from './api'
import BigNumber from "bignumber.js";


export class HttpServer {
    static account: string | null;


    /**
     *  登陆接口
     * @param sigHex
     * @param msg
     */
    static login(sigHex: string, msg: string,): Promise<any> {
        return NetAxios.post('/api/login', {user_id:this.account, sig_hex:sigHex, msg:JSON.parse(msg),});
    }


    /**
     *  查询仲裁数
     * @param sigHex
     * @param msg
     */
    static arbiter(): Promise<any> {
        return NetAxios.get('/api/arbiter', {userId:HttpServer.account});
    }

    /**
     *  登陆接口
     * @param sigHex
     * @param msg
     */
    static claim(sigHex: string, msg: string,): Promise<any> {
        return NetAxios.post('/api/claim', {user_id:this.account, sig_hex:sigHex, msg:JSON.parse(msg),});
    }



    /**
     *  申请成为仲裁者
     * @param sigHex
     * @param msg
     */
    static addArbiter(sigHex: string, msg: string,): Promise<any> {
        return NetAxios.post('/api/addArbiter', {user_id:HttpServer.account, sig_hex:sigHex, msg:JSON.parse(msg),});
    }


    /**
     *  减少仲裁资格
     * @param sigHex
     * @param msg
     */
    static pending(sigHex: string, msg: string,): Promise<any> {
        return NetAxios.post('/api/pending', {user_id:HttpServer.account, sig_hex:sigHex, msg:JSON.parse(msg),});
    }

    /**
     *  理赔仲裁申请
     * @param sigHex
     * @param msg
     */
    static claimApply(sigHex: string, msg: string,): Promise<any> {
        return NetAxios.post('/api/claimApply', {user_id:HttpServer.account, sig_hex:sigHex, msg:JSON.parse(msg),});
    }


    /**
     *  理赔仲裁申请查询
     */
    static claimListNew(before:number): Promise<any> {
        return NetAxios.get('/api/claimListNew', {userId:HttpServer.account,before:before * 20,after:before * 20 + 20,limit:20});
    }


    /**
     *  理赔仲裁申请查询
     */
    static claimListDown(before:number): Promise<any> {
        return NetAxios.get('/api/claimListDown', {userId:HttpServer.account ,before:before * 20,after:before * 20 + 20,limit:20});
    }


    static assessment(claimId:number): Promise<any> {
        return NetAxios.get('/api/assessment', {claimId});
    }


    static assessmentP(sigHex: string, msg: string,): Promise<any> {
        return NetAxios.post('/api/assessment', {user_id:HttpServer.account, sig_hex:sigHex, msg:JSON.parse(msg),});
    }

    // 第一轮投票后的查询
    static assessmentResult(claimId: string,): Promise<any> {
        return NetAxios.get('/api/assessmentResult', {claimId});
    }

    // 大约抵押保费金额等值的
    static challenge(claimId: string,): Promise<any> {
        return NetAxios.get('/api/challenge', {claimId});
    }

    // 发起挑战
    static challengeP(sigHex: string, msg: string,): Promise<any> {
        return NetAxios.post('/api/challenge', {user_id:HttpServer.account, sig_hex:sigHex, msg:JSON.parse(msg),});
    }



}
