export const MainWidget: React.FC = () => {
    const {request, data, status, setData} = useWait<any>(HttpServer.claimListNew);
    const {setTopData} = useContext(ClaimsProvider)

    useEffect(() => {
        request(0);
    }, [])

    useEffect(() => {
        if (data != null)
            setTopData(data);
    }, [data])

    return (
        <div>

            <WapStyle>
                <div className={'bar padding'}>
                    <div>CLAIMS ID</div>
                    <div>CLAIM BY</div>
                    <div>SUBMITTED</div>
                    <div>PRODUCT</div>
                    <div>COVER</div>
                    <div>STATE</div>
                    <div>ACTION</div>
                </div>
                <div className={'main'}>
                    {
                        status === HttpStatus.success ?
                            data?.claim_list ? data?.claim_list.map((ev: any, i: number) => <CellWidget
                                key={'iddasd' + i} bg={i % 2 !== 0}
                                model={ev}/>) : <NullStyle>N/A</NullStyle> :
                            [0, 0, 0, 0, 0, 0].map((ev, i) => <LoadingWidget key={'iddasd' + i} bg={i % 2 !== 0}/>)
                    }
                </div>
            </WapStyle>
        </div>
    );
}

const CellWidget: React.FC<{ bg: boolean, model: ClaimsListCell }> = ({bg, model}) => {
    const applyFn = useClaimApply(model.claim_id, model.product);
    const [status, setStaus] = useState(model.status);
    const load = useSendModal(async () => {
        let _data = await applyFn();
        setStaus(_data);
    }, SuccessWidget);
    const history = useHistory();


    return (
        <CellStyle bg={bg} className={'padding cell'} onClick={() => history.push('/claims/msgpage/' + model.claim_id)}>
            <div>{model.claim_id}</div>
            <div>{model.product}</div>
            <div>{DateUtils.dataString(model.submit_at)}</div>
            <div>{model.product}</div>
            <div>{DateUtils.dataString(model.begin_at)}</div>
            <div><StatusWidget status={status}/></div>
            <div>

                <ButtonWidget bgColor={'#0064FD'} disable={status != "new"} onClick={load} text={'Apply'} size={{x: 7, y: 25}}/>
            </div>
        </CellStyle>
    );
}


export const NullStyle = styled.div`
  padding: 20px;
  text-align: center;
`

const LoadingWidget: React.FC<{ bg: boolean, }> = ({bg,}) => {
    return (
        <CellStyle bg={bg} className={'padding cell'}>
            <div><Skeleton variant="text" height={20}/></div>
        </CellStyle>
    );
}


