import {useWeb3} from "../../provider/web3_provider";
import {useSendFn} from "../../utils/web3_send";
import {useCallback} from "react";
import BigNumber from "bignumber.js";

export const useCapitaStakeDeposit = () => {
    const {contrcts} = useWeb3();
    const handleSendFn = useSendFn(contrcts.stakeAddress);
    const handleDeposit = useCallback(async (amount:BigNumber) => {
        console.log(amount.toFixed())
        let method = contrcts.stakeContract.methods.deposit(amount.toFixed());
        let data = await handleSendFn(method);
        return data.transactionHash;
    }, [contrcts]);
    return [handleDeposit]
}
