import React from "react";
import styled from "styled-components";
import titleImg from "../../../assets/imgs/zhongcai_title.png"
import {TextField} from "@material-ui/core";
import {useForm} from "react-hook-form";
import {ButtonWidget} from "../../../widget/pub/button_widget";
import {useWait} from "../../../utils/wait_fn";
import {HttpServer} from "../../../net/http_server";
import {LoadingModal} from "../../../model/http_loading";
import {SuccessWidget} from "../arbitration/success_widget";

export const InputModel: React.FC<{ onDismiss?: () => void, callBack: Function ,title:string}> = ({onDismiss, callBack,title}) => {
    const {register, errors, handleSubmit} = useForm();
    let name = 'number'
    const {data, status, request} = useWait<any>(callBack);

    return (
        <LoadingModal status={status} data={data} onDismiss={onDismiss} success={SuccessWidget}>
            <InputStyle>
                <h2>{title}</h2>
                <div className={'box'}>
                    <h3>数量</h3>
                    <form>
                        <TextField name={name}
                                   error={Boolean(errors[name])}
                                   placeholder={'申请'}
                                   unselectable={'off'}
                                   fullWidth={true}
                                   inputRef={register({
                                       required: 'no Enter the number is dame',
                                   })}
                        />
                    </form>
                    <h4>申请一个仲裁资格将锁定您 <span>5000 Nsure</span></h4>
                    <div className={'bottons'}>
                        <ButtonWidget onClick={onDismiss} size={{y: 50, x: 10}} text={'取消'} type={'border'}/>
                        <ButtonWidget size={{y: 50, x: 10}}
                                      onClick={handleSubmit((data:any) => request(data.number))}
                                      text={'确定'}
                                      type={'color'}/>
                    </div>
                </div>
            </InputStyle>
        </LoadingModal>

    );
}


