import React, {useCallback, useEffect} from "react";
import styled from "styled-components";
import {Grid} from "@material-ui/core";
import youStake from '../../../assets/imgs/you_stake.svg'
import {ButtonWidget} from "../../../widget/pub/button_widget";
import {useHistory} from "react-router-dom";
import {CardInterface} from "../../../interface/personal/card";
import {useModel} from "../../../hook/use_model";
import {DepositModal} from "./deposit_modal";
import {useCapitaStakeDeposit} from "../../../web3/use_fn/capita_stake";
import BigNumber from "bignumber.js";
import {useWait} from "../../../utils/wait_fn";
import {HttpServer} from "../../../net/http_server";
import {useSignFn} from "../../../utils/sign_fn";
import {addArbiterJson} from "../fn/get_sign";
import {InputModel} from "./input_model";
import P1 from "../../../assets/imgs/p1.png"
import P2 from "../../../assets/imgs/p2.png"
import P3 from "../../../assets/imgs/p3.png"
import P4 from "../../../assets/imgs/p4.png"
import {getBalanceNumber} from "../../../utils/formatBalance";

export const TopWapWidget: React.FC = () => {

    const {data, request,setData} = useWait<any>(HttpServer.arbiter);
    const sign = useSignFn();

    const addArbiterFn = useCallback(async (method: string, fn: any, ev: string) => {
        let _params = addArbiterJson(method, ev);
        let _hex = await sign(_params);
        let _login = await fn(_hex, _params);
        setData(_login);
    }, [setData])

    const [onPending] = useModel(<InputModel
        title={"减少仲裁资格"}
        callBack={(ev: string) => addArbiterFn('pending', HttpServer.pending, ev)}/>)
    const [onDddArbiter] = useModel(<InputModel
        title={"申请仲裁资格"}
        callBack={(ev: string) => addArbiterFn('add_arbiter', HttpServer.addArbiter, ev)}/>)

    const [handleStake] = useCapitaStakeDeposit();
    const [handlePresent] = useModel(<DepositModal confirmationFn={async (ev:BigNumber)=>{
        let _hase = await handleStake(ev);
        return _hase
    }} />);




    useEffect(() => {

        request();
        const interval = setInterval(async () => {
            request();
        }, 20000);
        return () => clearInterval(interval)

    }, [])

    return (
        <Grid container spacing={3}>
            <CardWidget
                title={'Global'}
                oneTitle={"Total Deposits"}
                therrTitle={"Total Rewards"}
                oneUnit={"Nsure"}
                therrUnit={"ETH"}
                img={P1}
            >
                {/*<ButtonWidget onClick={historyPush}  size={{x: 8, y: 30}}  text={'Arbitration'} bgColor={'#584CCC'}/>*/}
            </CardWidget>
            <CardWidget
                title={'You Stake'}
                oneTitle={"Nsure Available"}
                oneUnit={"Nsure"}
                oneValue={getBalanceNumber(data?.available).toString()}
                img={P2}
                therrTitle={"Nsure Locked"}
            >
                <ButtonWidget size={{x: 8, y: 25}} onClick={handlePresent}  text={'Deposit'} bgColor={'#584CCC'}/>

                <ButtonWidget size={{x: 8, y: 25}}  text={'Withdraw'} bgColor={'#584CCC'}/>
            </CardWidget>
            <CardWidget
                img={P3}
                title={'仲裁'}
                oneTitle={"pending资格数"}
                therrTitle={"我的仲裁资格数"}
                oneValue={data?.pending}
                therrValue={data?.arbiter_sum}
            >
                <ButtonWidget size={{x: 8, y: 15}} onClick={onDddArbiter}  text={'申请成为仲裁者'} bgColor={'#584CCC'}/>

                <ButtonWidget size={{x: 8, y: 15}}  onClick={onPending} text={'减少仲裁资格'} bgColor={'#584CCC'}/>
            </CardWidget>
            <CardWidget
                title={'Your Rewards'}
                img={P4}
                oneTitle={"Rewards Earned"}
                therrTitle={"Rewards Claimable"}
                oneUnit={"ETH"}
                therrUnit={"ETH"}
            >
                <ButtonWidget size={{x: 8, y: 30}} text={'Claim'} bgColor={'#584CCC'}/>
            </CardWidget>
        </Grid>
    );
}


const CardWidget: React.FC<CardInterface> = ({
    title,
    oneTitle,
    therrTitle,
    oneUnit,
    therrUnit,
    oneValue,
    therrValue,
    img,
                                  children}) => {
    return (
        <CardStyle item md={3} sm={6} xs={12}>
            <div>
                <div className={'padding title'}>

                    <img src={img} alt=""/>
                    <span>{title}</span>
                </div>
                <div className={'padding body'}>
                    <div>
                        <h2>{oneTitle}</h2>
                        <h3>{oneValue || 0}<span>{oneUnit}</span></h3>
                    </div>
                    <div>
                        <h2>{therrTitle}</h2>
                        <h3>{therrValue || 0}<span>{therrUnit}</span></h3>
                    </div>
                </div>
                <div className={'buttons'}>
                    {children}
                </div>
            </div>

        </CardStyle>

    );
}

const CardStyle = styled(Grid)`
  > div {
    height: 300px;
    background-color: #F7F7FB;
    border-radius: 15px;
    padding: 8px;
    box-shadow: 0px 12px 31px 3px rgba(0, 0, 0, 0.1);

    .padding {
      padding-left: 55px;
    }

    .title {
      display: flex;
      padding-top: 10px;
      padding-bottom: 10px;
      align-items: center;

      img {
        width: 30px;
        height: 30px;
        display: flex;
        border-radius: 5px;
        margin-right: 20px;
      }

      span {
        font-size: 16px;
        font-weight: bold;
        color: #222222;
      }

      border-bottom: 1px solid #E9E9E9;
    }

    .body {
      > div {
        padding-top: 20px;

        h2 {
          font-size: 14px;
          font-weight: 400;
          color: #666666;
          line-height: 29px;
        }

        h3 {
          margin-top: 10px;
          line-height: 1;
          font-size: 18px;
          color: #270D4F;
          display: flex;
          align-items: baseline;

          span {
            font-size: 14px;
            color: #5D376B;
            line-height: 22px;
            display: block;
            margin-left: 10px;
          }
        }
      }

    }

    .buttons {
      display: flex;
      justify-content: space-around;
      margin-top: 30px;
      padding: 0 2%;
   
      box-sizing: border-box;
      p{
        font-size: 12px;
      }
    }
  }

`



