import React from "react";
import styled from "styled-components";
import {ButtonWidget} from "../widget/pub/button_widget";
import {getBalanceNumber} from "../utils/formatBalance";
import {useWait} from "../utils/wait_fn";
import {LoadingModal} from "./http_loading";

export const StakeModel: React.FC<{ onDismiss?: () => void, amount: string, handleChallenge: Function }> = ({
                                                                                                              onDismiss,
                                                                                                                amount,
                                                                                                              handleChallenge
                                                                                                          }) => {
    const {data, request, status} = useWait<any>(handleChallenge);
    return (
        <LoadingModal data={data} status={status}>
            <StakeStyle>
                <h3>申请发起挑战，需抵押10X保费金额等值的Nsure，您确认
                    发起挑战吗？</h3>

                <div className={'box'}>
                    <h2>抵押金额</h2>
                    <h4>{getBalanceNumber(amount)} <span>Nsure</span></h4>
                </div>
                <h5>Available Nsure : <span>xxxx</span><span>Nsure</span></h5>
                <div className={'buttons'}>
                    <ButtonWidget onClick={onDismiss} size={{x: 8, y: 35}} bgColor={'#0064FD'} text={'取消'}/>
                    <ButtonWidget onClick={request} size={{x: 8, y: 35}} bgColor={'#0064FD'} text={'确定'}/>
                </div>
            </StakeStyle>

        </LoadingModal>
    );
}


