import {useCallback, useState} from "react";


export const useWait = <T>(backFn: Function): { status: HttpStatus, request: any, data: T,setData:Function } => {
    const [status, setStatus] = useState<HttpStatus>(HttpStatus.start);
    const [data, setData] = useState<T | any>();

    const request = useCallback(async (...ev): Promise<any> => {
        try {
            setStatus(HttpStatus.wait);
            let data = await backFn(...ev);
            setData(data);
            setStatus(HttpStatus.success);
        } catch (e) {
            console.log(e);
            setData({err: e});
            setStatus(HttpStatus.failure);
        }

        return {status, data}
    }, [setStatus, setData]);


    return {
        status,
        request,
        data,
        setData
    };
}


export enum HttpStatus {
    start,
    wait,
    success,
    failure
}
