import React from "react";
import {TextField} from "@material-ui/core";
import styled from "styled-components";


export interface InputInterFace {
    label: string,
    unit?: string,
    onChange?: (e: any) => void,
    placeholder: string,
    setValue?: (ev: string) => void,
    inputRef: React.Ref<any>,
    errors: any;
    name?: string,
    buttonText?: string,
    type?: string,
    multiline?: boolean

    // max:string,
}


export const InputWidget: React.FC<InputInterFace> = ({
                                                          label,
                                                          placeholder,
                                                          setValue,
                                                          unit,
                                                          inputRef,
                                                          errors,
                                                          name,
                                                          buttonText = 'Max',
                                                          type,
                                                          onChange,
                                                          children,
                                                          multiline = false
                                                      }) => {


    return (
        <>
            <LabelStyle>{label}</LabelStyle>
            <FlexStyle>
                <InputStyle>
                    <TextField name={name || label}
                               error={Boolean(errors[name || label])}
                               placeholder={placeholder}
                               unselectable={'off'}
                               fullWidth={true}
                               inputRef={inputRef}
                               type={type}
                               rows={4}
                               multiline={multiline}
                               onChange={(ev) => onChange && onChange(ev)}
                    />
                    {
                        unit ?
                            <span>{unit}</span> : children
                    }
                </InputStyle>

                {setValue ?
                    <>
                        <SpacingStyle/>
                        <ButtonStyle onClick={() => setValue(name || label)}>
                            Max
                        </ButtonStyle>
                    </>
                    : <div/>}
            </FlexStyle>
            <HelperTextStyle>{errors[name || label] ? errors[name || label].message : ''}</HelperTextStyle>
        </>
    );
}


const ButtonStyle = styled.div`
  background-color: #2344EE;
  color: white;
  width: 80px;
  border-radius: 5px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  
`

const HelperTextStyle = styled.div`
  color: red;
  font-size: 14px;
  margin-top: 5px;
`;

const SpacingStyle = styled.div`
  width: 15px;
  height: 15px;

`

const LabelStyle = styled.h2`
  font-size: 14px;
  //font-weight: 400;
  line-height: 1;
  //font-family: Proxima Nova;
`


const FlexStyle = styled.div`
  display: flex;
  justify-content: space-between;
  margin-top: 10px;


`

const InputStyle = styled.div`
  display: flex;
  flex: 1;
  align-items: center;
  font-size: 14px;
  font-weight: 600;
  padding: 5px 10px;
  border-radius: 5px;
  border: 1px solid #EFEFF3;

  span {
    font-size: 14px;
    font-weight: 400;
    display: inline-block;
    padding-left: 20px;
  }

  .MuiInput-underline:before {
    display: none;
  }

  .MuiInput-underline:after {
    display: none;
  }

  .MuiInputBase-input {
    color: black;
    text-align: end;


    &::-webkit-input-placeholder {
      text-align: start;
    }
  }
`
